.navigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  height: 60px;
  padding: 8px 0;
  box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.logo {
  width: 5%;
  margin-right: 16px;
}
.list {
  display: flex;
  flex-direction: row;
  list-style: none;
}
.list li a{
  font-family: sans-serif;
  font-size: 19px;
  font-weight: 600px;
  width: 60px;
  margin-right: 16px;
  text-decoration: none;
  color: rgb(95, 92, 92);
}
.list li a:hover {
  color: tomato;
}
.active {
  color: tomato;
}
@media (max-width: 601px) {
  .navigation {
    width: 300px;
    padding: 16px 4px;
  }

}